/* //// TYPOGRAPHY //// */
$font-family-title: Arial, Helvetica, sans-serif;
$font-family-subtitle: "Times New Roman", Times, serif;
$font-family-base: $font-family-title;

$font-size-smaller: 0.625rem; /* 10px */
$font-size-small: 0.75rem; /* 12px */
$font-size-medium: 0.875rem; /* 14px */
$font-size-base: 1rem; /* 16px */
$font-size-regular: 1.125rem; /* 18px */
$font-size-big: 1.5rem; /* 24px */
$font-size-bigx: 1.75rem; /* 28px */
$font-size-title: 2rem; /* 32px */
$font-size-subtitle: 2.5rem; /* 40px */
$font-size-giant: 4rem; /* 64px */

$font-weight-lighter: 100;
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-bold: 700;
$font-weight-ultrabold: 900;

$line-height-small: 1;
$line-height-medium: 1.5;
$line-height-big: 2;

/* //// COLORS //// */
$color-black: #000000;
$color-white: #ffffff;
$color-purple: #340d4c;

$color-red: #ff0000;
$color-red-1: #ed1a3b;

$color-blue-hover: #eff2fe;
$color-blue-0: #8693f3;
$color-blue-1: #0d3e80;
$color-blue-2: #093366;
$color-blue-3: #051933;
$color-blue-4: #514cdf;
$color-blue-5: #453ec4;
$color-blue-6: #333280;
$color-blue-7: #25245a;

$color-golden-1: #daa520;
$color-golden-2: #80610d;
$color-golden-3: #332e05;

$color-gray-1: #efefef;
$color-gray-2: #d9d9d9;
$color-gray-3: #cccccc;
$color-gray-4: #808080;
$color-gray-5: #6c6b78;
$color-gray-6: #383838;
$color-gray-7: #333333;
$color-gray-8: #111111;

$color-black-transparency: #000000b3;
$color-white-transparency: #ffffffb3;
$color-white-transparency-1: #fcfcfca4;
$color-white-transparency-2: #ffffff0f;

$color-linear-gradient-1: linear-gradient(
        265.62deg,
        $color-blue-6 4.97%,
        $color-blue-4 96.79%
    ),
    linear-gradient(0deg, $color-blue-0, $color-blue-0);
$color-linear-gradient-2: linear-gradient(
    264.97deg,
    #ed1a3b -3.18%,
    #fe354b 100%
);
$color-linear-gradient-3: linear-gradient(
        112.83deg,
        #ffffff78 0%,
        #ffffff00 110.84%
    ),
    radial-gradient(121.49% 277.08% at 0% 0%, #ffffffb3 0%, #ffffff00 100%);

$main-bg-color: $color-white;
$primary-color: $color-blue-6;
$secondary-color: $color-red-1;
$terciary-color: $color-white;
$text-dark-color: $color-gray-5;
$text-light-color: $color-gray-3;

/* //// TRANSITIONS //// */
$transition: all 0.4s ease;
$transition-1s: all 1s ease;

/* //// BOX-SHADOW //// */
$boxshadow-hover-1: 1px 1px 20px -2px $color-white-transparency-2;
$boxshadow-slide-golden_down: inset 0 100px 0 0 $primary-color;
$boxshadow-slide-golden_right: inset 400px 0 0 0 $primary-color;
$boxshadow-slide-golden_left: inset 0 0 0 50px $primary-color;
$boxshadow-slide-golden_diagonal: inset 400px 50px 0 0 $primary-color;
$boxshadow-slide-white_down: inset 0 100px 0 0 $secondary-color;
$boxshadow-slide-white_right: inset 400px 0 0 0 $secondary-color;
$boxshadow-slide-white_left: inset 0 0 0 50px $secondary-color;
$boxshadow-slide-white_diagonal: inset 400px 50px 0 0 $secondary-color;
