&-items {
    &__container {
        width: 75%;
        margin: 0 auto 5.25rem;
        text-align: center;
        @include media("xs") {
            width: 100%;
        }
        .slick {
            &-slide {
                margin: 0;
            }
        }
    }
    &__list {
        .slick {
            &-slide {
                margin: 0 0.5rem;
            }
            @include media("xs") {
                &-arrow {
                    opacity: 1;
                    visibility: visible;
                    cursor: pointer;
                }
                &-prev {
                    left: -1rem;
                }
                &-next {
                    right: -1rem;
                }
            }
        }
    }
    &__item {
        padding: 0 1.25rem;
        border-radius: 0.625rem;
    }
}
