.title {
    font-family: $font-family-title;
    font-size: $font-size-medium;
    font-weight: $font-weight-regular;
    color: $color-blue-5;
    text-transform: uppercase;
    width: fit-content;
    position: relative;
    left: 2.5rem;
    &::before,
    &::after {
        content: "";
        display: inline-block;
        width: 1.875rem;
        height: 1px;
        margin: 0 1ch;
        background: $color-blue-5;
        position: absolute;
        top: 50%;
    }
    &::before {
        right: 100%;
    }
    &--main {
        color: $secondary-color;
        &::before,
        &::after {
            background: $secondary-color;
        }
    }
    @include media("xs") {
        width: fit-content;
        max-width: 100%;
    }
    @include media("lg") {
        font-size: 1vw;
    }
}
.subtitle {
    font-family: $font-family-subtitle;
    font-size: $font-size-subtitle;
    font-weight: $font-weight-regular;
    color: $primary-color;
    @include media("xs") {
        font-size: $font-size-title;
    }
    @include media("lg") {
        font-size: 3.3vw;
    }
    &--giant {
        color: $color-blue-7;
        @include media("sm") {
            font-size: $font-size-giant;
        }
        @include media("lg") {
            font-size: 5vw;
        }
    }
    &--big {
        font-size: $font-size-big;
        @include media("xs") {
            font-size: $font-size-big;
        }
        @include media("lg") {
            font-size: 2vw;
        }
    }
}
.description,
li {
    font-family: $font-family-base;
    font-size: $font-size-base;
    font-weight: $font-weight-regular;
    color: $text-dark-color;
    line-height: $line-height-medium;
    margin-bottom: $font-size-base;
    &--bold {
        font-weight: $font-weight-bold;
    }
    @include media("lg") {
        font-size: 1.2vw;
    }
}
.not-before {
    &::before {
        display: none;
    }
}
.not-after {
    &::after {
        display: none;
    }
}
.bg-blue {
    background: $primary-color;
    .container {
        padding: 5.25rem 0;
        @include media("xs") {
            width: 100%;
            padding: 4rem 1rem;
        }
    }
    .title,
    .subtitle,
    .description {
        color: $terciary-color;
        &::before,
        &::after {
            background: $terciary-color;
        }
        &::selection {
            color: $terciary-color;
            background: $secondary-color;
        }
    }
    .btn {
        background: $color-linear-gradient-2;
    }
}
