&-employees {
    &__container {
        width: 75%;
        margin: 0 auto 5.25rem;
        text-align: center;
        @include media("xs") {
            width: 100%;
        }
        .slick {
            &-slide {
                margin: 0;
            }
        }
    }
    &__title {
        left: 0;
        display: inline-block;
    }
    &__subtitle {
        padding: 1.5rem 0 2rem;
    }
    &__item {
        padding: 0 1.25rem;
    }
}
