&-donation {
    &__container {
        width: 75%;
        margin: 0 auto 5.25rem;
        padding: 5.25rem 0;
        @include media("xs") {
            width: 100%;
            padding: 4rem 1rem 10rem;
            margin-bottom: 4rem;
        }
        &.grid {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            align-items: center;
            justify-content: space-between;
            @include media("xs") {
                grid-template-columns: 1fr;
                justify-content: center;
                align-items: center;
                position: relative;
            }
            .grid__left {
                display: flex;
                flex-direction: column;
                gap: 1.5rem;
            }
        }
    }
    &__title {
        left: 2.5rem;
    }
    &__chart-container {
        max-width: 23.5rem;
        margin: 0 auto;
        @include media("xs") {
            margin: 3rem auto 1.5rem;
            max-width: 100%;
        }
        @include media("lg") {
            canvas {
                &#anacDonations {
                    width: 30vw !important;
                    height: 30vw !important;
                }
            }
        }
    }
    .legend {
        &-container {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            gap: 1rem 2rem;
            @include media("xs") {
                position: absolute;
                bottom: 4rem;
                justify-content: center;
            }
            @include media("lg") {
                gap: 2vw;
            }
        }
        &-item {
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 0.5rem;
            cursor: pointer;
            &:active {
                .legend-label {
                    text-decoration: underline;
                }
            }
            @include media("lg") {
                gap: 0.6vw;
            }
        }
        &-color {
            width: $font-size-base;
            height: $font-size-base;
            border-radius: 0.25rem;
            border: 0;
            @include media("lg") {
                width: 1.2vw;
                height: 1.2vw;
            }
        }
        &-label {
            font-family: $font-family-base;
            font-size: $font-size-base;
            color: $terciary-color;
            @include media("lg") {
                font-size: 1.2vw;
            }
        }
    }
}
