&-instagram {
    &__container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1.5rem;
        width: 75%;
        margin: 0 auto 5.25rem;
        padding: 5.25rem 0 3rem;
        @include media("xs") {
            width: 100%;
            padding: 4rem 1rem;
            margin-bottom: 4rem;
        }
        #eapps-instagram-feed-1 {
            width: 100%;
            @include media("xs") {
                max-height: 10.625rem;
            }
            > a {
                display: none !important;
                filter: opacity(0);
                cursor: default;
            }
        }
        .eapps-instagram-feed {
            &-title-container {
                display: none;
            }
            &-posts-slider-nav {
                opacity: 0;
                transition: $transition;
                &:hover {
                    opacity: 0.5;
                }
            }
        }
    }
    &__title {
        left: 0;
    }
}
