.anac-structure {
    @import "../../../components/tipography/tipography";

    .structure {
        @import "./main";
        @import "./support_house";
        @import "./headquarters";
    }
    .flex-container {
        @include flex-container(column, center, stretch, 1rem);
        margin: 0;
    }
    .grid {
        &__container {
            width: 100%;
            @include grid-container(35%, 4rem);
            place-content: stretch;
            @include media("xs") {
                grid-template-columns: 1fr;
                gap: 1.5rem;
            }
        }
        @include media("xs") {
            &-row {
                &-2 {
                    grid-row: 2;
                }
            }
        }
    }
    .slick {
        &-slide {
            margin-bottom: 1rem;
            @include media("xs") {
                margin: 0;
            }
        }
    }
    .container {
        width: 75%;
        margin: 4rem auto;
        @include media("xs") {
            width: 90vw;
            margin: 2.5rem auto;
        }
    }
    .title {
        left: 0;
    }
    .description {
        margin: 0;
    }
    .list {
        margin: 0;
        @include media("xs") {
            margin-top: 0.5rem;
            max-width: 90vw;
        }
    }
    iframe {
        border-radius: 12px;
        width: -webkit-fill-available;
        @include media("xs") {
            width: 90vw;
        }
    }
    img {
        border-radius: 12px;
        box-sizing: border-box;
    }
}
