@mixin media($point) {
    @if $point == "sm" {
        @media (min-width: 768px) {
            @content;
        }
    } @else if $point == "md" {
        @media (min-width: 992px) {
            @content;
        }
    } @else if $point == "lg" {
        @media (min-width: 1440px) {
            @content;
        }
    } @else if $point == "xmd" {
        @media (max-width: 991px) {
            @content;
        }
    } @else if $point == "xs" {
        @media (max-width: 767px) {
            @content;
        }
    } @else if $point == "xxs" {
        @media (max-width: 425px) {
            @content;
        }
    }
}
@mixin flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin flex-container(
    $direction: row,
    $justify: flex-start,
    $align: stretch,
    $gap: initial
) {
    display: flex;
    flex-direction: $direction;
    justify-content: $justify;
    align-items: $align;
    gap: $gap;
}
// Exemplo de uso:
