@mixin grid-container($columns: 1fr, $gap: 10px) {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax($columns, 1fr));
    grid-gap: $gap;
    @include media("xs") {
        grid-template-columns: 1fr;
    }
}
