.slick {
    &-slide {
        user-select: text;
        margin: 0 0.5rem;
    }
    &-arrow {
        z-index: 2;
        color: $primary-color;
        transition: $transition;
        @include media("xs") {
            opacity: 0;
            visibility: hidden;
            cursor: none;
        }
        &::before {
            content: "";
            font-family: "Material Symbols Outlined";
            font-size: 1.5rem;
            color: $primary-color;
            display: inline-block;
            width: 100%;
            height: 100%;
            line-height: 1.3rem;
            opacity: 1;
        }
        &[aria-disabled="true"] {
            &::before {
                opacity: 0.5;
            }
        }
    }
    &-prev {
        left: -1.5rem;
        &::before {
            // background: url(../../../img/svg/slick_arrow-prev.svg) no-repeat
            //     center;
            // background-size: contain;
            content: "\e5cb";
            // background-color: $color-black;
        }
    }
    &-next {
        right: -1.5rem;
        &::before {
            // background: url(../../../img/svg/slick_arrow-next.svg) no-repeat
            //     center;
            // background-size: contain;
            content: "\e5cc";
            // background-color: $color-black;
        }
    }
    &-dots {
        bottom: 0;
        li {
            button {
                &::before {
                    font-size: 0.7rem;
                    color: $primary-color;
                }
            }
        }
    }
}
