&-about-us {
    &__container {
        width: 75%;
        margin: 0 auto 5.25rem;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 4.375rem;
        @include media("xs") {
            width: 100%;
            padding: 0 1rem 0;
            margin-bottom: 4rem;
            grid-template-columns: 1fr;
            gap: 2.5rem;
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        gap: 1.5rem;
        @include media("xs") {
            grid-row: 1;
            padding: 0;
        }
    }
    &__title {
        left: 0;
    }
    &__link {
        padding: 1rem 3rem;
    }
}
