&-main {
    &__container {
        .grid {
            @include grid-container(45%, 1.5rem);
            justify-items: center;
            @include media("xs") {
                @include grid-container(100%, 1.5rem);
            }
            &__top {
                grid-column: 1 / -1;
            }
        }
    }
    &__content {
        margin: 4rem 0;
        align-items: center;
        &.grid {
            grid-template-columns: 2fr 1.5fr;
            margin: 2.5rem auto;
        }
    }
}
