#cta {
    @import "../../components/tipography/tipography";
    @import "../../components/button/button";
    .cta {
        padding: 0 1rem;
        &__container {
            width: 90%;
            margin: 0 auto 5.25rem;
            padding: 4rem;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background: url(https://www.anacar.org.br/wp-content/uploads/2024/03/banner_cta-D.webp)
                no-repeat;
            background-position: center;
            background-size: cover;
            border-radius: 20px;
            transition: $transition-1s;
            @include media("md") {
                width: 75%;
            }
            @include media("xs") {
                min-height: 21.5rem;
                padding: 2rem;
                margin-bottom: 4rem;
                background: url(https://www.anacar.org.br/wp-content/uploads/2024/03/banner_cta-M.webp)
                    no-repeat;
                background-position: center;
                background-size: cover;
            }
        }
        &__subtitle {
            &.subtitle {
                width: 100%;
                margin: 0 auto 1.5rem;
                color: $terciary-color;
                @include media("xs") {
                    margin-bottom: 1rem;
                }
            }
        }
    }
}
