&-team {
    &__content {
        text-align: center;
        &:not(&:last-of-type) {
            margin-bottom: 2rem;
        }
        &.flex-container {
            @include flex-container(column, center, center, 1.5rem);
        }
    }
    &__title,
    &__subtitle,
    &__description {
        text-align: center;
        margin: 0;
    }
    &__title {
        display: inline-block;
    }
    &__list {
        margin-top: 1rem;
        .slick {
            &-slide {
                margin: 0 0.5rem;
            }
            @include media("xs") {
                &-arrow {
                    opacity: 1;
                    visibility: visible;
                    cursor: pointer;
                }
                &-prev {
                    left: -1rem;
                }
                &-next {
                    right: -1rem;
                }
            }
        }
    }
    &__item {
        border: 1px solid #e0dffe;
        padding: 1.5rem;
        border-radius: 12px;
        min-height: 8rem;
        @include media("xs") {
            padding: 1rem;
        }
        p,
        span {
            font-family: $font-family-base;
        }
        p {
            font-size: 1.25rem;
            color: $primary-color;
            margin-bottom: 0.75rem;
        }
        span {
            font-size: $font-size-medium;
        }
    }
    // &__name {
    //     min-height: 4ch;
    // }
}
