&__container {
    width: 100vw;
    margin: 0;
    padding: 0.5rem 1rem;
    align-items: center;
    transition: $transition;
    &:has(.header__links.active) {
        background: $color-white;
    }
    &.is-fixed {
        width: 100vw;
        padding: 0.5rem 1rem;
        background: $color-white;
        box-shadow: 0px 2px 20px 2px $color-white-transparency-1;
    }
}
&__logo {
    &-link {
        svg {
            height: 4rem;
            width: 8rem;
        }
    }
}
&__links {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 2rem;
    width: 0;
    height: 100vh;
    position: absolute;
    top: 5rem;
    right: 0;
    transition: 0.6s;
    z-index: 11;
    background: $color-white;

    opacity: 0;
    visibility: hidden;
    overflow-y: hidden;
    pointer-events: none;
    &.active {
        width: 100vw;
        opacity: 1;
        visibility: visible;
        overflow-y: auto;
        pointer-events: all;
    }
    .dropdown [data-toggle="dropdown"] {
        position: relative;
        display: block;
    }
    .dropdown .dropdown-menu {
        overflow: hidden;
        list-style: none;
        max-height: 0;
    }
    .dropdown .dropdown-menu li a {
        display: block;
    }
    .dropdown .show {
        display: block;
        max-height: 9999px;
    }
    .dropdown .hide {
        max-height: 0;
    }
}
&__menu {
    width: 100%;
    transition: $transition;
    a {
        padding: 1rem;
        width: 100%;
    }
    &--btn {
        display: flex;
        position: absolute;
        right: 1.875rem;
    }
    &-items {
        flex-direction: column;
        gap: 0;
        max-width: 100%;
        padding: 1rem 0;
        border-radius: 0;
        border: 1px solid $color-gray-2;
        border-left: 0;
        border-right: 0;
        &-link {
            padding: 1rem 0;
            width: 100vw;
        }
    }
    &-item {
        right: 0;
        &#menuItem {
            &-sobrenos {
                .icon-arrow {
                    &::after {
                        left: 12ch;
                    }
                }
            }
            &-servicos {
                .icon-arrow {
                    &::after {
                        left: 10ch;
                    }
                }
            }
            &-doacoes {
                .icon-arrow {
                    &::after {
                        left: 24ch;
                    }
                }
            }
        }
        &-link {
            width: 100vw;
            .icon-arrow {
                &.close::after {
                    -moz-transform: rotate(45deg);
                    -ms-transform: rotate(45deg);
                    -webkit-transform: rotate(45deg);
                    transform: rotate(45deg);
                    -moz-transition: -moz-transform 0.6s;
                    -o-transition: -o-transform 0.6s;
                    -webkit-transition: -webkit-transform 0.6s;
                    transition: transform 0.6s;
                }
                &.open::after {
                    -moz-transform: rotate(-135deg);
                    -ms-transform: rotate(-135deg);
                    -webkit-transform: rotate(-135deg);
                    transform: rotate(-135deg);
                    -moz-transition: -moz-transform 0.6s;
                    -o-transition: -o-transform 0.6s;
                    -webkit-transition: -webkit-transform 0.6s;
                    transition: transform 0.6s;
                }
            }
        }
        &-subitem {
            padding: 0 1rem;
            background: $color-blue-hover;
        }
        &.dropdown .show,
        &.dropdown .hide {
            -moz-transform-origin: 50% 0%;
            -ms-transform-origin: 50% 0%;
            -webkit-transform-origin: 50% 0%;
            transform-origin: 50% 0%;
        }
        &.dropdown .show {
            display: block;
            max-height: 9999px;
            -moz-transform: scaleY(1);
            -ms-transform: scaleY(1);
            -webkit-transform: scaleY(1);
            transform: scaleY(1);
            -moz-transition: $transition;
            -o-transition: $transition;
            -webkit-transition: $transition;
            transition: $transition;
        }
        &.dropdown .hide {
            max-height: 0;
            -moz-transform: scaleY(0);
            -ms-transform: scaleY(0);
            -webkit-transform: scaleY(0);
            transform: scaleY(0);
            -moz-transition: $transition;
            -o-transition: $transition;
            -webkit-transition: $transition;
            transition: $transition;
        }
    }
}
&__cta {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    &--btn {
        width: 100%;
        margin: 0 0.9rem;
    }
}
