.footer {
    background: $color-gray-8;
    &__title {
        color: $color-white;
        font-family: "Times New Roman", Times, serif;
        font-size: $font-size-big;
        margin-bottom: 1rem;
        @include media("lg") {
            font-size: 2vw;
        }
    }
    &__link,
    &__text {
        font-family: $font-family-base;
        font-size: $font-size-medium;
        color: $color-gray-4;
        line-height: 1.5;
        text-decoration: none;
        transition: $transition;
        &:hover,
        &:focus {
            color: $text-light-color;
        }
        @include media("lg") {
            font-size: 1vw;
        }
    }
    &__list {
        display: flex;
    }
    &__container {
        width: 85%;
        margin: 0 auto;
        padding: 3rem 0 1rem;
        position: relative;
        &-flex {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            transition: $transition;
            @include media("xs") {
                flex-direction: column;
                align-items: center;
                gap: 2rem;
            }
            &.border-bottom {
                border-bottom: 1px solid #33336b;
                padding-bottom: 3rem;
                margin-bottom: 3rem;
                @include media("xs") {
                    border: 0;
                    margin: 0;
                }
            }
            &.flex-end {
                justify-content: flex-end;
                padding-top: 1.5rem;
                @include media("xs") {
                    padding-top: 1rem;
                }
            }
        }
    }
    &__logo {
        flex: 1;
        &--link {
            svg {
                width: 9rem;
                height: 5rem;
                display: flex;
                align-items: flex-start;
                @include media("lg") {
                    font-size: 1.2vw;
                    width: 100%;
                    height: auto;
                }
                use {
                    margin-right: auto;
                }
            }
        }
    }
    &__links {
        flex: 1;
        @include media("xs") {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 80%;
        }
        &-item {
            flex: 1;
            @include media("xs") {
                flex: auto;
                width: 100%;
            }
        }
        &--list {
            flex-direction: column;
            gap: 0.5rem;
            @include media("xs") {
                flex-direction: column;
                align-items: center;
                width: 100%;
            }
        }
        &--link,
        &--text {
            line-height: $line-height-big;
            @include media("xs") {
                text-align: center;
                display: block;
                width: 100%;
            }
        }
    }
    &__rightReserved {
        &--text {
            @include media("xs") {
                display: block;
                text-align: center;
            }
            &:hover,
            &:focus {
                color: $color-gray-4 !important;
            }
        }
        &--link {
            display: inline;
        }
    }
    &__socials {
        &--list {
            flex-direction: row;
            gap: 1.2rem;
        }
        &--link {
            svg {
                height: 1.5rem;
                width: 1.5rem;
                @include media("lg") {
                    width: 1.5vw;
                    height: 1.5vw;
                }
                use {
                    fill: $color-white;
                    transition: $transition;
                }
                &:hover {
                    use {
                        fill: $color-gray-4;
                    }
                }
            }
        }
    }
    &__development {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 1ch;
        &--text {
            font-size: $font-size-smaller;
            letter-spacing: 1.3px;
            &:hover {
                color: $color-gray-4;
            }
        }
        &--img {
            svg {
                width: 1.5rem;
                height: 1.7rem;
                opacity: 0.3;
                transition: $transition;
                @include media("lg") {
                    width: 1.5vw;
                    height: auto;
                }
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}
