.anac-employees {
    @import "../../../components/slick-slide/slick-slide";
    @import "../../../components/tipography/tipography";

    .employees {
        @import "./main";
        @import "./employees_items";
    }
    .flex-container {
        @include flex-container(row, center, center, 1rem);
        margin: 0;
    }
    .grid {
        &__container {
            width: 100%;
            @include grid-container(35%, 1.5rem);
            place-content: stretch;
            @include media("xs") {
                grid-template-columns: 1fr;
                gap: 1.5rem;
            }
        }
        &__item {
            &--top {
                grid-column: 1 / -1;
            }
        }
        @include media("xs") {
            &-row {
                &-2 {
                    grid-row: 2;
                }
            }
        }
    }
    .slick {
        &-slide {
            margin-bottom: 1rem;
            @include media("xs") {
                margin: 0;
            }
        }
    }
    .container {
        width: 75%;
        margin: 4rem auto;
        @include media("xs") {
            width: 90vw;
            margin: 2.5rem auto;
        }
    }
    .list {
        margin: 0;
        @include media("xs") {
            margin-top: 0.5rem;
            max-width: 90vw;
        }
    }
}
