&-bazar {
    .grid {
        &__container {
            width: 100%;
            @include grid-container(30%, 2rem);
            align-items: center;
            @include media("xxs") {
                grid-template-columns: 1fr;
                gap: 1.5rem;
            }
        }
    }
    &__content {
        margin-bottom: 2rem;
        @include flex-container(column, center, center, 1rem);
        @include media("xs") {
            text-align: center;
        }
    }
    &__title {
        left: 0;
    }
    &__description {
        margin: 0;
    }
    &__list {
        margin-top: 1rem;
        .slick {
            &-slide {
                margin: 0 0.5rem;
            }
        }
    }
    &__item {
        border-radius: 12px;
        figure {
            width: 100%;
            height: auto;
        }
        img {
            border-radius: 12px;
            box-sizing: border-box;
        }
    }
    iframe {
        width: -webkit-fill-available;
        border-radius: 12px;
        @include media("xs") {
            max-height: 30vh;
        }
    }
}
