.btn {
    padding: 1rem 2rem;
    font-family: $font-family-base;
    font-size: $font-size-base;
    font-weight: $font-weight-bold;
    color: $color-white;
    background: $color-linear-gradient-1;
    border: 2px solid $color-blue-0;
    border-radius: 0.625rem;
    outline: none;
    cursor: pointer;
    transition: $transition;
    &:hover,
    &:focus-visible {
        opacity: 0.9;
    }
    @include media("lg") {
        font-size: 1.2vw;
        padding: 1vw 2vw;
        border-radius: 0.9vw;
    }
}
