&-items {
    &__item {
        @include flex-container(column, center, flex-start, 1rem);
    }
    &__head {
        @include flex-container(row, center, center, 1rem);
    }
    &__subitem {
        list-style: disc;
        font-family: $font-family-base;
        margin-left: 2rem;
        &:not(:last-of-type) {
            margin: 0 0 0 2rem;
        }
    }
    &__icon {
        width: 3rem;
        height: 3rem;
        background: $color-linear-gradient-1;
        border: 2px solid $color-blue-0;
        border-radius: 0.625rem;
        @include media("lg") {
            height: 3vw;
            width: 3vw;
            border-width: 0.1vw;
            svg {
                max-width: calc(3vw - 0.2vw);
                max-height: calc(3vw - 0.2vw);
                padding: 0.7vw;
            }
        }
        svg {
            max-width: calc(3rem - 4px);
            max-height: calc(3rem - 4px);
            padding: 0.625rem;
        }
    }
}
