&-headquarters {
    .grid {
        &__container {
            @include media("sm") {
                grid-template-columns: 40% auto;
            }
        }
    }
    .container {
        padding: 4rem 0;
    }
    &__title {
        left: 2.5rem !important;
    }
    &__item {
        border-radius: 12px;
        vertical-align: middle;
        figure {
            width: 100%;
            height: auto;
        }
    }
    &__maps {
        height: 100%;
    }
}
