&-numbers {
    .grid__container {
        grid-template-columns: 1fr;
    }
    &__content {
        margin: 1rem auto 2rem;
    }
    &__select {
        padding: 0.5rem 1rem;
        border-radius: 12px;
        border: 1px solid;
        color: $primary-color;
        @include media("xs") {
            width: 90vw;
        }
    }
    &__table {
        width: 100%;
        caption {
            margin-bottom: 1rem;
            text-align: left;
            @include media("xs") {
                width: 90vw;
            }
        }
        &--row {
            vertical-align: middle;
        }
        &--data {
            vertical-align: middle;
        }
        &--last-years {
            table {
                width: 100%;
                border-collapse: collapse;
            }
            tr {
                &:nth-child(even) {
                    background-color: $color-gray-1;
                }
            }
            th {
                font-family: $font-family-base;
                font-weight: $font-weight-bold;
                color: $primary-color;
                background: $color-gray-1;
            }
            th,
            td {
                border: 1px solid #ddd;
                text-align: center;
                padding: 0.5rem;
            }
            @include media("xs") {
                + .scroll-arrow {
                    height: 0;
                    width: 40px;
                    border: 1px solid $text-dark-color;
                    position: relative;
                    left: 35vw;
                    animation: scroll 1.5s infinite;
                    -webkit-animation: scroll 1.5s infinite;
                    margin-top: 1rem;
                    &::after {
                        content: "";
                        display: block;
                        position: absolute;
                        left: 100%;
                        top: -5px;
                        height: 1px;
                        width: 10px;

                        // triangle
                        border-left: 10px solid $text-dark-color;
                        border-top: 5px solid transparent;
                        border-bottom: 5px solid transparent;
                    }
                }
                @keyframes scroll {
                    0% {
                        width: 40px;
                    }
                    30% {
                        width: 70px;
                    }
                    60% {
                        width: 40px;
                    }
                }
                @-webkit-keyframes scroll {
                    0% {
                        width: 40px;
                    }
                    30% {
                        width: 70px;
                    }
                    60% {
                        width: 40px;
                    }
                }
            }
        }
        .description {
            border-bottom: 1px solid #ddd;
            white-space: normal;
            &--bold {
                font-size: $font-size-title;
                padding-right: 1rem;
                color: $primary-color;
                text-align: right;
                white-space: nowrap;
            }
        }
    }
    .table-responsive {
        @include media("xs") {
            position: relative;
            overflow-x: auto;
            overflow-y: auto;
            max-width: 100%;
            padding-bottom: 1rem;
        }
    }
    .table-resume {
        &__content {
            border: 1px solid $primary-color;
            border-radius: 12px;
            width: max-content;
            padding: 2rem;
            margin: 1rem auto;
            .description {
                margin: 0;
            }
        }
    }
}
