.anac-about {
    @import "../../../components/button/button";
    @import "../../../components/slick-slide/slick-slide";
    @import "../../../components/tipography/tipography";

    .about {
        @import "./main";
        @import "./history";
        @import "./mission";
        @import "./numbers";
        @import "./team";
    }
    .container {
        width: 75%;
        margin: 4rem auto;
        @include media("xs") {
            width: 90vw;
            margin: 2.5rem auto;
        }
    }
    .grid {
        &__container {
            @include grid-container(40%, 1.5rem);
            align-items: center;
            @include media("xs") {
                @include grid-container(100%, 1.5rem);
            }
        }
        &__item {
            &--top {
                grid-column: 1 / -1;
            }
        }
    }
    .title {
        left: 0;
        &--main {
            left: 2.5rem;
        }
    }
}
