&-form {
    .flex {
        &-container {
            @include flex-container(column, center, stretch, 1rem);
        }
    }
    &__subtitle {
        margin-bottom: 1.5rem;
    }
    &__label {
        display: block;
        margin: 0.625rem 0;
        line-height: 1;
    }
    &__input,
    &__textarea {
        width: 100%;
        border-radius: 8px;
        border: 0;
        outline: none;
        padding: $font-size-base;
        font-family: $font-family-base;
        color: $text-dark-color;
    }
}
