.anac-contact {
    @import "../../../components/button/button";
    @import "../../../components/tipography/tipography";

    .contact {
        @import "./main";
        @import "./contact_form";
        @import "./location";
    }
    .container {
        width: 75%;
        margin: 4rem auto;
        @include media("xs") {
            width: 90vw;
            margin: 2.5rem auto;
        }
    }
    .grid {
        &__container {
            @include grid-container(45%, 1.5rem);
            align-items: center;
            @include media("xs") {
                @include grid-container(100%, 1.5rem);
            }
        }
        &__item {
            &--top {
                grid-column: 1 / -1;
            }
        }
    }
    .title {
        left: 0;
        &--main {
            left: 2.5rem;
        }
    }
}
