&-supportHouse {
    .grid {
        &__container {
            @include media("sm") {
                grid-template-columns: auto 40%;
            }
        }
    }
    &__item {
        border-radius: 12px;
        vertical-align: middle;
        figure {
            width: 100%;
            height: auto;
        }
    }
    &__maps {
        height: 100%;
    }
}
