&-stepByStep {
    &__container {
        text-align: start;
    }
    .text-align-left {
        @include media("sm") {
            text-align: left;
        }
    }
    .list {
        counter-reset: section;
        list-style-type: none;
        @include media("xs") {
            margin: 0 auto;
            width: 90%;
        }
    }
    &__item {
        @include media("xs") {
            width: fit-content;
            margin: 0 auto 1rem;
        }
        &::before {
            counter-increment: section;
            content: counters(section, " - ") " - ";
            background: transparent !important;
        }
    }
}
