&-oficinas {
    &__container {
        padding: 4rem 0;
    }
    &__content {
        @include flex-container(column, center, flex-start, 1rem);
        .grid {
            &__container {
                width: 100%;
                @include grid-container(30%, 2rem);
                @include media("xxs") {
                    grid-template-columns: 1fr;
                    gap: 1.5rem;
                }
            }
        }
    }
    &__image {
        position: relative;
        source,
        img {
            border-radius: 0.8rem;
        }
        &:hover,
        &:focus-visible {
            figcaption {
                transform: scale(1.05);
            }
        }
    }
    &__legend {
        font-family: $font-family-subtitle;
        font-size: $font-size-big;
        color: $terciary-color;
        position: absolute;
        bottom: 1.25rem;
        left: 1.25rem;
        transition: $transition;
    }
    &__title {
        left: 2.5rem;
    }
}
