.header {
    background: transparent;
    @include media("xs") {
        background: $color-white;
        @import "header_mobile";
    }
    &__container {
        width: 85%;
        margin: 0 auto;
        padding: 1rem 0 0;
        position: relative;
        z-index: 10;

        display: grid;
        grid-template-columns: auto 3fr;
        align-self: center;
        gap: 2rem;
        transition: $transition-1s;
        &.is-fixed {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            width: 85%;
            padding: 0;
            -webkit-backdrop-filter: blur(0.15rem);
            backdrop-filter: blur(0.15rem);
        }
    }
    &__logo {
        width: 1fr;
        &-link {
            svg {
                height: 5rem;
                width: 9rem;
                use {
                    filter: drop-shadow(0px 0px 1px $color-white);
                }
            }
        }
    }
    &__links {
        display: grid;
        grid-template-columns: 3fr auto;
        align-self: center;
        gap: 2rem;
        transition: $transition;
    }
    &__menu {
        a {
            font-family: $font-family-base;
            font-weight: $font-weight-regular;
            color: $color-blue-7;
            text-decoration: none;
            display: inline-block;
            transition: $transition;
            border: none;
            outline: none;
            &:has(.icon) {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 0.5rem;
            }
        }
        &--btn {
            display: none;
            color: $color-white;
            background: $color-linear-gradient-1;
            border: 2px solid $color-blue-0;
            border-radius: 0.625rem;
            padding: 0.75rem;
            &.active {
                #menu-burger_icon {
                    border-top: transparent;
                    transform: rotate(0deg);
                    &::before {
                        transform: rotate(135deg);
                    }
                    &::after {
                        transform: rotate(-135deg);
                        top: -7px;
                    }
                }
            }
            #menu-burger_icon {
                display: block;
                border-top: 2px solid $color-white;
                width: 20px;
                transition: 0.4s;
                &::before,
                &::after {
                    content: "";
                    display: block;
                    width: 20px;
                    height: 2px;
                    margin-top: 5px;
                    background: $color-white;
                    position: relative;
                    transition: 0.4s;
                }
            }
        }
        &-items {
            max-width: max-content;
            margin: auto;
            display: flex;
            flex-direction: row;
            gap: 1.5rem;
            background: $color-linear-gradient-3;
            border-radius: 2rem;
            padding: 0 1rem;
        }
        &-item {
            position: relative;
            &-link {
                padding: 1rem;
                @include media("sm") {
                    &:hover,
                    &:focus {
                        transform: scale(1.03);
                    }
                }
                .icon-arrow {
                    &::after {
                        content: "";
                        display: inline-block;
                        position: absolute;
                        top: 35%;
                        right: -0.5rem;
                        width: 0.5rem;
                        height: 0.5rem;
                        border: 1px solid $color-blue-7;
                        border-left: none;
                        border-top: none;
                        transform: rotate(45deg);
                        transition: $transition;
                    }
                }
            }
            &-sub {
                @include media("sm") {
                    position: absolute;
                    left: 1rem;
                    top: 3rem;
                    z-index: 11;
                    width: auto;
                    min-height: 5.625rem;
                    display: flex;
                    justify-content: center;
                    background: $color-white;
                    transition: $transition;
                    opacity: 0;
                    visibility: hidden;
                    pointer-events: none;
                    &item {
                        a {
                            padding: 0.75rem 1.5rem 1rem;
                            min-width: 9rem;
                            width: 100%;
                            &:hover,
                            &:focus {
                                background: $color-blue-hover;
                            }
                        }
                    }
                }
            }
            @include media("sm") {
                &:hover,
                &:focus,
                &:focus-visible {
                    .is-submenu {
                        opacity: 1;
                        visibility: visible;
                        pointer-events: auto;
                    }
                    .icon-arrow {
                        &::after {
                            transform: rotate(-135deg);
                            top: 45%;
                        }
                    }
                }
            }
        }
        svg {
            height: 1rem;
            width: 1rem;
            use {
                fill: $primary-color;
                transition: $transition;
            }
        }
    }
    &__cta {
        @import "../../components/button/button.scss";
    }
}
.overflow-hidden {
    overflow: hidden;
}
