&-main {
    &__content {
        margin: 4rem 0;
        @include media("xs") {
            margin: 2.5rem 0;
        }
        &.grid {
            &__container {
                align-items: flex-start;
                @include media("sm") {
                    grid-template-columns: 2fr 1.5fr;
                }
            }
        }
    }
}
