.anac-services {
    @import "../../../components/tipography/tipography";
    @import "../../../components/slick-slide/slick-slide";

    .services {
        @import "./main";
        @import "./services_items";
        @import "./bazar";
        @import "./oficinas";
    }
    .grid {
        &__container {
            @include grid-container(45%, 1.5rem);
            @include media("xs") {
                @include grid-container(100%, 1.5rem);
            }
        }
        &__item {
            &--top {
                grid-column: 1 / -1;
            }
        }
    }
    .container {
        width: 75%;
        margin: 4rem auto;
        @include media("xs") {
            width: 90vw;
            margin: 2.5rem auto;
        }
    }
}
