&-services {
    &__container {
        width: 75%;
        margin: 0 auto 5.25rem;
        @include media("xs") {
            width: 100%;
            padding: 0 1rem 0;
            margin-bottom: 4rem;
        }
        .title,
        .subtitle,
        .description {
            text-align: center;
        }
        &.flex {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            gap: 1.5rem;
        }
        &--grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 4.375rem;
            margin-top: 1.5rem;
            @include media("xs") {
                grid-template-columns: 1fr;
                gap: 2.5rem;
            }
        }
    }
    &__title {
        left: 0;
    }
    &__item {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 1.25rem 0.75rem;
    }
    &__icon {
        width: 3rem;
        height: 3rem;
        background: $color-linear-gradient-1;
        border: 2px solid $color-blue-0;
        border-radius: 0.625rem;
        @include media("lg") {
            height: 3vw;
            width: 3vw;
            border-width: 0.1vw;
            svg {
                max-width: calc(3vw - 0.2vw);
                max-height: calc(3vw - 0.2vw);
                padding: 0.7vw;
            }
        }
        svg {
            max-width: calc(3rem - 4px);
            max-height: calc(3rem - 4px);
            padding: 0.625rem;
        }
    }
}
