&-materials {
    .description {
        margin: 0;
    }
    &__card {
        &-item {
            text-align: left;
            padding: 1.7rem;
            margin: 0;
            border-radius: 0.75rem;
            border: 1px solid $color-blue-6;
            @include media("xs") {
                &.item-third {
                    grid-row: 4;
                }
                &.item-fourth {
                    grid-row: 3;
                }
            }
            .title {
                margin-bottom: 1rem;
                font-size: 1.25rem;
                font-weight: $font-weight-bold;
                text-transform: none;
            }
        }
    }
}
