&-main {
    &__banner {
        &--container {
            position: relative;
            top: -7.5rem;
            @include media("xs") {
                top: -5.2rem;
            }
            .slick {
                &-slide {
                    margin: 0;
                }
                &-prev {
                    height: 1.25rem;
                    left: 2rem;
                }
                &-next {
                    height: 1.25rem;
                    right: 2rem;
                }
            }
        }
        &--figure {
            position: relative;
            &.is-dark {
                img {
                    filter: brightness(0.5);
                }
                .title,
                .subtitle {
                    color: $color-white;
                    &::before,
                    &::after {
                        background: $color-white;
                    }
                    &::selection {
                        color: $terciary-color;
                        background: $secondary-color;
                    }
                }
            }
            @include media("lg") {
                img {
                    width: -webkit-fill-available;
                }
            }
        }
        &--content {
            display: flex;
            flex-direction: column;
            gap: 1.5rem;
            max-width: 60%;
            position: absolute;
            top: 24%;
            left: 12%;
            z-index: 3;
            @include media("xs") {
                max-width: 80%;
                top: auto;
                bottom: 1.5rem;
                left: 1rem;
            }
        }
        &--title {
            &.title {
                left: 2.5rem;
                color: $secondary-color;
                &::before,
                &::after {
                    background: $secondary-color;
                }
            }
        }
    }
    &__cta {
        &--list {
            display: flex;
            flex-direction: row;
            gap: 1.2rem;
        }
        &--link {
            width: max-content;
            @import "../../../components/button/button";
            &.has-figure {
                padding: 0.5rem;
                border: 1px solid $color-white;
                border-radius: 50%;
                color: $primary-color;
                background: $color-linear-gradient-3;
                &:hover,
                &:focus-visible {
                    opacity: 0.9;
                }
                @include media("lg") {
                    padding: 1vw;
                    figure {
                        svg {
                            height: 2vw;
                            width: 2vw;
                        }
                    }
                }
                figure {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    svg {
                        height: 1.5rem;
                        width: 1.5rem;
                        use {
                            fill: $primary-color;
                            transition: $transition;
                        }
                    }
                }
            }
        }
    }
}
