&-mission {
    &__content {
        @include flex-container(column, center, center, 1.5rem);
    }
    &__title,
    &__subtitle,
    &__description {
        text-align: center;
        margin: 0;
    }
    &__list {
        @include grid-container(30%, 1.5rem);
        margin-top: 1.5rem;
        align-items: center;
        @include media("xs") {
            @include grid-container(100%, 3rem);
        }
    }
    &__item {
        margin-bottom: 0;
    }
}
