&-location {
    .grid {
        &__container {
            width: 100%;
            @include grid-container(45%, 2rem);
            align-items: center;
            @include media("xxs") {
                grid-template-columns: 1fr;
                gap: 1.5rem;
            }
        }
    }
    .flex-container {
        @include flex-container(column, center, center, 1rem);
    }
    &__content {
        width: 100%;
        @include flex-container(column, center, center, 1rem);
        @include media("xs") {
            text-align: center;
        }
    }
    &__description {
        margin: 0;
    }
    iframe {
        width: -webkit-fill-available;
        border-radius: 12px;
        @include media("xs") {
            max-height: 30vh;
        }
    }
}
