&-qrcodePix {
    .grid {
        &__item {
            &--left {
                @include media("xs") {
                    grid-row: 2;
                }
            }
        }
        &__container {
            @include media("sm") {
                align-items: center;
                justify-items: start;
                grid-template-columns: 30% 1fr;
                text-align: start;
            }
        }
    }
    &__figure {
        &.qr-figure {
            svg {
                width: 100%;
                min-height: 18.75rem;
            }
        }
    }
    &__data {
        background: #c9d4fc;
        padding: 4px;
        border-radius: 0.75rem;
        box-sizing: border-box;
        display: grid;
        grid-template-columns: 2fr 1fr;
        align-items: center;
        @include media("xxs") {
            grid-template-columns: 1fr 1fr;
        }
    }
    &__description {
        &#qrcode-link {
            color: $primary-color;
            margin: 0;
            padding: 0 1rem 0 0.8rem;
            text-overflow: ellipsis;
            overflow: hidden;
            display: inline-block;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            white-space: nowrap;
        }
    }
    &__btn {
        padding: 1rem 1.5rem;
        box-sizing: border-box;
        white-space: nowrap;
        @include flex-container(row, center, center, 0.75rem);
        &-icon {
            height: 1.3rem;
            svg {
                width: 1rem;
                height: 1.3rem;
            }
        }
    }
}
