.anac-donationsNF {
    @import "../../../components/tipography/tipography";

    .donationsNF {
        @import "./main";
        @import "./step_by_step";
    }
    .flex-container {
        @include flex-container(column, center, stretch, 1rem);
        margin: 0;
    }
    .grid {
        &__container {
            @include grid-container(35%, 1.5rem);
            place-content: stretch;
            justify-items: center;
            width: 100%;
            @include media("xs") {
                grid-template-columns: 1fr;
            }
        }
        &__item {
            &--top {
                grid-column: 1 / -1;
            }
        }
        @include media("xs") {
            &-row {
                &-2 {
                    grid-row: 2;
                }
            }
        }
    }
    .container {
        width: 75%;
        margin: 4rem auto 0;
        text-align: center;
        @include media("xs") {
            width: 90vw;
            margin: 2.5rem auto 0;
        }
    }
    .subtitle {
        margin-bottom: 1.5rem;
    }
    .title {
        left: 0;
    }
}
